export default () => {
    const list = [...document.querySelectorAll('#fix-header .item')]
    const scrollTo = function (y) {
        window.scrollTo({
            top: y,
            behavior: 'smooth',
        })
    }
    list.filter(x => x.dataset.no !== undefined).forEach(x => {
        x.addEventListener('click', function (evt) {
            const no = parseInt(evt.target.dataset.no)
            scrollTo(rangeListGen()[no])
        })
    })
    const rangeListGen = function () {
        const max = Math.max
        const height = window.innerHeight
        return [
            41,                                                                               // 完美主义者的乐园
            920 - 41,                                                                                 // AI × 脑科学
            1300 + max(height, 920) - 41,                                                              // AI × APP
            1300 + max(height, 920) + max(height, 1300) - 41,                                          // AI × 艺术
            1740 + max(height, 920) + max(height, 1300) + max(height, 1300) - 41,                       // AI × 开发者
            1030 + max(height, 920) + max(height, 1300) + max(height, 1300) + max(height, 1740) - 41,   // 大事件
            1618 + max(height, 920) + max(height, 1300) + max(height, 1300) + max(height, 1740) + max(height,1030) - 41,   // 大事件
        ]
    }
    const scrollHandler = function () {
        const offset = window.pageYOffset + 300

        const rangeList = rangeListGen()

        let index = 0

        if (offset > rangeList[0]) index = 0
        if (offset > rangeList[1]) index = 1
        if (offset > rangeList[2]) index = 2
        if (offset > rangeList[3]) index = 3
        if (offset > rangeList[4]) index = 4
        if (offset > rangeList[5]) index = 5
        if (offset > rangeList[6]) index = 6

        const hightLightClass = 'hight-light'

        list.forEach(x => x.classList.remove(hightLightClass))
        list[index].classList.add(hightLightClass)
    }
    scrollHandler()
    window.addEventListener('scroll', scrollHandler)
}